.MovieList-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0 30px;
    text-align: center;
    width: 50vw;
}

img.Movie-image {
  max-width: 100%;
  -webkit-box-shadow: 8px 8px 5px grey;
          box-shadow: 8px 8px 5px grey;
  /* border-radius: 100px; */
}

p.Movie-title {
  font-size: 18px;
  line-height: 0;
  padding-bottom: 10px;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
}


